.leaflet-container {
  height: 100vh;
  left: 0;
  position: absolute !important;
  right: 0;
  top: 0%;
  -webkit-transition: all .5s ease-in-out;
  transition: all .5s ease-in-out;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  width: 100%; }
  .leaflet-container.show-content {
    -webkit-transform: translate(0%, 0);
            transform: translate(0%, 0);
    width: 60%; }
  .leaflet-container .leaflet-control button {
    background: #96281b;
    border: 1px solid #96281b;
    padding: 10px 20px;
    color: #FFF;
    font-size: 15px; }
  .leaflet-container .leaflet-control input[type=checkbox] {
    height: 0;
    width: 0;
    visibility: hidden; }
  .leaflet-container .leaflet-control label {
    cursor: pointer;
    text-indent: -9999px;
    width: 60px;
    height: 30px;
    background: grey;
    display: block;
    border-radius: 15px;
    position: relative; }
  .leaflet-container .leaflet-control label:after {
    content: '';
