    top: 3px;
    left: 3px;
    width: 23px;
    height: 23px;
    background: #fff;
    border-radius: 30px;
    -webkit-transition: 0.3s;
    transition: 0.3s; }
  .leaflet-container .leaflet-control input:checked + label {
    background: #96281b; }
  .leaflet-container .leaflet-control input:checked + label:after {
    left: calc(100% - 5px);
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%); }
  .leaflet-container .leaflet-control label:active:after {
    width: 23px; }

